@use 'base/variables'as v;
@use 'base/mixins'as m;

.navegacion {
    @include m.tablet{
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        gap: 3rem;
        padding: 2rem 0;
        border-bottom: 1px solid darken(v.$gris, 20%);
    }
    &-enlace {
        display: block;
        text-align: center;
        padding: 1rem 0;
        font-weight: v.$bold;
        &:hover{
            color: darken($color: v.$negro, $amount: 10%);
        }
    }
}