html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: Poppins, sans-serif;
  color: black;
  font-size: 1.6rem;
}

.contenedor {
  max-width: 120rem;
  max-width: 90%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

h1, h2, h3 {
  font-family: Poppins, sans-serif;
  margin: 0 0 2rem 0;
  font-weight: 700;
  color: black;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2.3rem;
}

p {
  color: black;
  font-size: 2rem;
}

.barra-lateral {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.barra-lateral .redes-sociales {
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 0;
}
@media (min-width: 760px) {
  .barra-lateral .redes-sociales {
    flex-direction: column;
    align-items: center;
  }
}
.barra-lateral .redes-sociales li {
  list-style: none;
  padding: 1rem 0;
}
.barra-lateral img {
  margin: 1.5rem 0;
}

@media (min-width: 760px) {
  .contenido {
    display: grid;
    grid-template-columns: 15% 85%;
  }
}
.contenido .barra-lateral {
  max-height: 100vh;
}
@media (min-width: 760px) {
  .contenido .barra-lateral {
    height: 100vh;
    border-right: 1px solid #E5E5E5;
    border-bottom: 1px solid #E5E5E5;
    box-shadow: 0.2rem 0.2rem 1rem #b2b2b2;
  }
}
.contenido .contenido-principal {
  width: 100%;
}

@media (min-width: 760px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 3rem;
    padding: 2rem 0;
    border-bottom: 1px solid #b2b2b2;
  }
}
.navegacion-enlace {
  display: block;
  text-align: center;
  padding: 1rem 0;
  font-weight: 700;
}
.navegacion-enlace:hover {
  color: black;
}

@media (min-width: 760px) {
  .contenedor-grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    place-items: center;
  }
}
.contenedor-grid h1 {
  margin-top: 2rem;
  color: #27517c;
  font-size: 5rem;
  line-height: 1.5;
  text-align: left;
}
.contenedor-grid .card-img {
  width: 100%;
  background-color: red;
  padding: 12rem;
  border-radius: 1rem;
}
@media (min-width: 760px) {
  .contenedor-grid .card-img {
    width: auto;
    transform: translateY(15rem);
  }
}
.contenedor-grid .card-rocket {
  width: 100%;
  padding: 12rem;
}
@media (min-width: 760px) {
  .contenedor-grid .card-rocket {
    width: auto;
    transform: translateY(15rem);
  }
}

.tecnologias .tecnologia {
  margin: 2rem 0;
  border-radius: 1rem;
  padding: 2rem;
  box-shadow: 0.5rem 0.5rem 1rem #b2b2b2;
  transition: transform 300ms ease;
}
@media (min-width: 760px) {
  .tecnologias .tecnologia {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .tecnologias .tecnologia:nth-child(2) {
    transform: translateX(9rem);
  }
  .tecnologias .tecnologia img {
    margin: 0;
  }
}
.tecnologias .tecnologia-contenido h3 {
  text-align: left;
}
.tecnologias .tecnologia:hover {
  transform: scale(1.1);
}

.sobre-mi h2 {
  font-size: 3rem;
  font-weight: 700;
  text-align: left;
  color: #27517c;
}
.sobre-mi .mi-contenido {
  box-shadow: 0.5rem 0.5rem 1rem #b2b2b2;
  border-radius: 1rem;
  padding: 2rem;
  transition: transform 300ms ease;
}
.sobre-mi .mi-contenido a {
  font-weight: 700;
}
.sobre-mi .mi-contenido a:hover {
  color: #1b3855;
}
.sobre-mi .mi-contenido:hover {
  transform: scale(1.1);
}

.proyectos {
  margin: 5rem 0;
}
.proyectos h2 {
  color: #27517c;
  font-size: 3rem;
}
.proyectos img {
  margin: 2rem 0;
  object-fit: fill;
  object-position: center center;
  height: 100%;
  border-radius: 1rem;
}
@media (min-width: 760px) {
  .proyectos-cards {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.footer {
  background-color: red;
  height: 3rem;
}