@use 'base/variables'as v;
@use 'base/mixins'as m;

.contenedor-grid {
    @include m.tablet {
        @include m.grid(2, 2rem);
        place-items: center;
    }

    h1 {
        margin-top: 2rem;
        color: v.$azul;
        font-size: 5rem;
        line-height: 1.5;
        text-align: left;
    }

    .card-img {
        width: 100%;
        background-color: red;
        padding: 12rem;
        border-radius: 1rem;
        @include m.tablet {
            width: auto;
            transform: translateY(15rem);
        }
    }

    .card-rocket {
        width: 100%;
        padding: 12rem;

        @include m.tablet {
            width: auto;
            transform: translateY(15rem);
        }
    }
}

.tecnologias {
    .tecnologia {
        margin: 2rem 0;
        border-radius: 1rem;
        padding: 2rem;
        box-shadow: 0.5rem 0.5rem 1rem darken(v.$gris, 20%);
        transition: transform 300ms ease;

        @include m.tablet {
            &:nth-child(2) {
                transform: translateX(9rem);
            }

            display: flex;
            align-items: center;
            gap: 2rem;

            img {
                margin: 0;
            }
        }

        &-contenido {
            h3 {
                text-align: left;
            }
        }

        &:hover{
            transform: scale(1.1);
        }
    }
}

.sobre-mi {
    
    h2 {
        font-size: 3rem;
        font-weight: v.$bold;
        text-align: left;
        color: v.$azul;
    }

    .mi-contenido {
        box-shadow: 0.5rem 0.5rem 1rem darken(v.$gris, 20%);
        border-radius: 1rem;
        padding: 2rem;
        transition: transform 300ms ease;

        a {
            font-weight: v.$bold;

            &:hover {
                color: darken($color: v.$azul, $amount: 10%);
            }
        }
        &:hover{
            transform: scale(1.1);
        }
    }

}