@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra-lateral{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    .redes-sociales{
        width: 90%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        padding: 0;
        @include m.tablet{
            flex-direction: column;
            align-items: center;
        }
        li{
            list-style: none;
            padding: 1rem 0;
        }
    }

    img{
        margin: 1.5rem 0;
    }
}