@use 'base/variables' as v;
@use 'base/mixins' as m;

.proyectos{
    margin: 5rem 0;
    h2{
        color: v.$azul;
        font-size: 3rem;
    }
    img{
        margin: 2rem 0;
        object-fit: fill;
        object-position: center center;
        height: 100%;
        border-radius: 1rem;
    }
    @include m.tablet{
        &-cards{
            @include m.grid(2, 2rem);
        }
    }

}