@use 'base/variables' as v;

html{
    box-sizing: border-box;
    font-size: 62.5%;
}

*, *::before, *::after{
    box-sizing: inherit;
}

body{
    font-family: v.$main-font;
    color: v.$negro;
    font-size: 1.6rem;
}

.contenedor{
    max-width: 120rem;
    max-width: 90%;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

img{
    display: block;
    max-width: 100%;
}