@use 'base/variables' as v;

h1, h2, h3{
    font-family: v.$main-font;
    margin: 0 0 2rem 0;
    font-weight: v.$bold;
    color: v.$negro;
    text-align: center;
}

h1{
    font-size: 3rem;
}
h2{
    font-size: 2.6rem;
}
h3{
    font-size: 2.3rem;
}

p{
    color: v.$negro;
    font-size: 2rem;
}