@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido{
    @include m.tablet{
        display: grid;
        grid-template-columns: 15% 85%;
    }

    .barra-lateral{
        max-height: 100vh;
        @include m.tablet{
            height: 100vh;
            border-right: 1px solid v.$gris;
            border-bottom: 1px solid v.$gris;
            box-shadow: 0.2rem 0.2rem 1rem darken(v.$gris, 20%);
        }
    }
    .contenido-principal{
        width: 100%;
    }
}